import React, { Component } from "react";

import { connect } from "react-redux";
import {Field, formValueSelector, reduxForm} from "redux-form";
import {InputDate, ReanderSelect} from "../../../../components/helpers/field";
import { getTglSystem,formatGram } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
import LaporanKirimBarangExel from "./exel/LaporanKirimBarangExel";
import Loading from 'react-fullscreen-loading';
import CetakLaporanKirimBarang from "./pdf/CetakLaporanKirimBarang";
import {getMasterToko} from "../../../../actions/datamaster_action";

class HeadLaporanKirimBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    // this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    CetakLaporanKirimBarang(this.props.LaporanDataBarang, this.props.tujuan);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  componentDidMount() {
    this.props.dispatch(getMasterToko())
  }

  render() {
    let databarang = [];
    let berat = 0;
    this.props.LaporanDataBarang.forEach((element) => {
      databarang.push(element);
      berat = berat + element.berat
    });
    const columns = [
  
      {
        dataField: "kode_toko",
        text: "Baki Asal",
        footerAttrs: {
          colSpan: "5",
        },
        footer: (cell, row, num, index) => {
          return <div>Total Barang : {databarang.length} </div>;
        },
      },
      {
        dataField: "kode_barang",
        text: "Kode Barang",
      },
      {
        dataField: "nama_barang",
        text: "Nama Barang",
      },

      {
        dataField: "berat",
        text: "Berat",
        headerClasses: 'text-right',
        formatter: (cell) => {
          return <div className="text-right"> {formatGram(cell, 2)} </div>;
        },
      },
      {
        dataField: "kadar_cetak",
        text: "Kadar Cetak",
        headerClasses: 'text-right',
        formatter: (cell) => {
          return <div className="text-right"> {cell} </div>;
        },
      },
     
      {
        dataField: "harga_jual",
        text: "Harga Jual",
        headerClasses: 'text-right',
        formatter: (cell) => {
          return <div className="text-right"> {cell.toLocaleString("kr-KO")} </div>;
        },
      }
    ];
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>
          <div className="col-lg-4">
            <Field
                label="Tujuan"
                name="tujuan"
                options={this.props.dataToko.map((data) => {
                  let hasil = {
                    value: data.kode_toko,
                    name: data.kode_toko,
                  };
                  return hasil;
                })}
                placeholder={"Silahkan Pilih Tujuan"}
                component={ReanderSelect}
            />
          </div>
          <div className="col-lg-12 row justify-content-end mb-4">
            <div className="col-lg-4">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
           <div className="col-12">
            <Tabel
              keyField="no_pindah"
              data={this.props.export ? databarang  :  []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Pindah Barang Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  type="button"
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanKirimBarangExel data={this.props.LaporanDataBarang} />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadLaporanKirimBarang = reduxForm({
  form: "HeadLaporanKirimBarang",
    enableReinitialize: true,
  validate: ValidasiTanggalLaporan 
})(HeadLaporanKirimBarang);

const selector = formValueSelector("HeadLaporanKirimBarang")
export default connect((state) => {
  return {
    dataToko: state.datamaster.getMasterToko,
    tujuan : selector(state, "tujuan"),
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadLaporanKirimBarang);
